<template>
    <div class="login">
        <h2>Login CMS</h2>
        <form method="post" action="/auth/login" name="login">
            <div class="form-group">
                <div class="input-group">
                    <input type="text" id="email" required="required" v-model="email"/>
                    <label class="control-label" for="email">Email</label><i class="bar"></i>
                </div>
            </div>
            <div class="form-group">
                <div class="input-group">
                    <input type="password" id="password" required="required" v-model="password"/>
                    <label class="control-label" for="password">Password</label><i class="bar"></i>
                </div>
            </div>
            <div class="d-flex flex-column flex-lg-row align-items-center justify-content-between down-container">
                <button class="btn btn-primary" @click.prevent="login" type="submit">
                    Log In

                </button>
                <!--
                <router-link class='link' :to="{name: 'Signup'}">Create account</router-link>
                -->
            </div>
        </form>
    </div>
</template>

<script>

    import Axios from 'axios';

    export default {
        name: 'login',
        data(){
            return {
                email: 'admin@gmail.com',
                password: 'iii3studi1'
            }
        },
        methods: {
            login(){

                let submitData = {
                    email: this.email,
                    password: this.password
                };

                Axios
                    .post('http://localhost:9001/v1/api/admin/login', submitData, {
                        withCredentials: true
                    })
                    .then((res) => {
                        if (res.status == 200) {
                            if (res.data.token) {
                                window.location.href = '/';
                            }
                        }
                    })
                    .catch((err) => {

                    });
            }
        }
    }
</script>

<style lang="scss">
    @import '../../../sass/variables';
    @import '../../../../node_modules/bootstrap/scss/mixins/breakpoints';
    @import '../../../../node_modules/bootstrap/scss/variables';

    .login {
        @include media-breakpoint-down(md) {
            width: 100%;
            padding-right: 2rem;
            padding-left: 2rem;
            .down-container {
                .link {
                    margin-top: 2rem;
                }
            }
        }

        h2 {
            text-align: center;
        }
        width: 21.375rem;

        .down-container {
            margin-top: 3.125rem;
        }
    }
</style>



